.ic_vote {
    width: 16px;
    height: 16px;
    transform: translateY(-5px);
}

.voted_down {
    transform: rotate(180deg) translateY(1px);
}

.button_vote {
    padding: 5px;
    cursor: pointer;
    display: inline;
}
.button_vote:last-child {
    margin-left: 10px;
}
.button_vote svg {
    width: 24px;
    height: 24px;
}